  display: -webkit-box;
  display: -moz-box;
  display: -ms-flexbox;
  display: -moz-flex;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
  flex-wrap: wrap;
}

.login {
  display: flex;
  justify-content: center;
  position: relative;
  top: 50%;
  transform: translateY(50%);
}

.image20 {
@media screen and (max-width:950px){
